.docxview {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navpaging {
  background-color: transparent;
  align-items: center;
  justify-content: center;
  display: flex;
}
.navpaging nav {
  box-shadow: 0px 0px 8px 1px rgba(0, 0, 0, 0.2);
}
.page-item.active .page-link {
  background-color: #0d6efd;
}
.downloadbtn {
  position: absolute;
  top: 10px;
  right: 10px;
}
.pdfview {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
